/* Navigation */

nav ul {
  margin-top: -4px;
  text-align: right;
}
nav > ul > li {
  display: inline-block;
  margin-right: -1px;
  position: relative;
}
nav ul li a {
  display: inline-block;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
  position: relative;
  padding: 40px 25px;
}
nav ul li a:before {
  content: "";
  background: rgba(255, 255, 255, 0.1);
  width: 100%;
  height: 43px;
  border-radius: 43px;
  position: absolute;
  left: 0;
  top: calc(50% - 21px);
  opacity: 0;
  transition: all 0.3s;
  z-index: -1;
}

nav ul li a:hover:before {
  opacity: 1;
}
nav ul li:hover > ul {
  opacity: 1;
  visibility: visible;
}
nav ul li:hover > ul li {
  margin-top: 0;
}
nav ul li a:hover,
nav ul li a.active {
  color: var(--purple);
  text-decoration: none;
}

nav ul ul {
  position: absolute;
  top: 100%;
  left: 0;
  width: 215px;
  box-shadow: 0px 15px 60px -19px black;
  opacity: 0;
  visibility: hidden;
  z-index: 99;
  text-align: left;
}
nav ul ul li {
  padding: 10px 15px;
  background-color: #202120;
  border-bottom: 1px solid #292929;
  margin-top: 20px;
  position: relative;
  transition: all 0.5s;
  position: relative;
}
nav ul ul li i {
  position: absolute;
  right: 10px;
  top: 18px;
  color: #fff;
}
nav ul ul li a {
  padding: 5px;
  color: #fff;
}
nav ul ul li:last-child {
  border-bottom: 0;
}
nav ul ul ul {
  left: 100%;
  top: 0px;
  transition: all 0.5s;
}
