--------------------------------------------------------------*/
.contact-pg-contact-section {
  position: relative;
}

.contact-pg-contact-section .contact-details ul {
  margin-top: 50px;
}

.contact-pg-contact-section .contact-details ul li {
  position: relative;
  padding-left: 70px;
}

.contact-pg-contact-section .contact-details ul>li {
  margin-top: 45px;
}

.contact-pg-contact-section .icon {
  width: 50px;
  height: 50px;
  line-height: 50px;
  position: absolute;
  left: 0;
  top: 0;
  -webkit-transform: scale(1);
          transform: scale(1);
  text-align: center;
  border-radius: 5px;
  background: #202120;
}

.contact-pg-contact-section .icon i {
  font-size: 20px;
  font-size: 1.25rem;
  color: #d044ff;
}

.contact-pg-contact-section .contact-details h5 {
  font-size: 20px;
  font-size: 1.25rem;
  margin: 0 0 0.3em;
}

.contact-pg-contact-section .contact-form {
  margin-top: 40px;
}

@media (max-width: 991px) {
  .contact-pg-contact-section .contact-form {
      margin-top: 40px;
  }
}

@media (max-width: 767px) {
  .contact-pg-contact-section .contact-form {
      margin-top: 30px;
  }
}

.contact-pg-contact-section .contact-form input,
.contact-pg-contact-section .contact-form textarea {
  background: #202120;
  width: 100%;
  height: 45px;
  border: 1px solid transparent;
  color: #898888;
  border-radius: 0;
  padding: 6px 18px;
  box-shadow: none;
}

.contact-pg-contact-section .contact-form form textarea {
  height: 170px;
}

.contact-pg-contact-section .contact-form input:focus,
.contact-pg-contact-section .contact-form textarea:focus {
  box-shadow: none;
  border-color: #f67d4a;
}

.contact-pg-contact-section .contact-form form {
  margin: 0 -15px;
}

.contact-pg-contact-section .contact-form form  .row{
  width: 100%;
  margin: 0;
}

.contact-pg-contact-section .contact-form form ::-webkit-input-placeholder {
  font-style: 16px;
  font-style: normal;
  color: #898888;
}

.contact-pg-contact-section .contact-form form :-moz-placeholder {
  font-style: 16px;
  font-style: normal;
  color: #898888;
}

.contact-pg-contact-section .contact-form form ::-moz-placeholder {
  font-style: 16px;
  font-style: normal;
  color: #898888;
}

.contact-pg-contact-section .contact-form form :-ms-input-placeholder {
  font-style: 16px;
  font-style: normal;
  color: #898888;
}

.contact-pg-contact-section .contact-form form>div {
  width: calc(50% - 30px);
  float: left;
  margin: 0 15px 30px;
}



@media (max-width: 550px) {
  .contact-pg-contact-section .contact-form form>div {
      width: calc(100% - 30px);
      float: none;
  }
}

.contact-pg-contact-section .contact-form form>.submit-area {
  margin-bottom: 0;
}

.contact-pg-contact-section .contact-form form .comment-area {
  width: calc(100% - 30px);
  float: none;
}

.contact-pg-contact-section .contact-form form .comment-area textarea {
  height: 170px;
}

.contact-pg-contact-section .contact-form form .comment-area textarea:focus {
  border-color: #f67d4a;
  outline: none;
}

.contact-pg-contact-section .theme-btn-s2,
.contact-pg-contact-section .theme-btn-s4 {
  border: 0;
  outline: 0;
}

.contact-pg-contact-section .contact-map {
  height: 450px;
  margin-top: 75px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

@media (max-width: 991px) {
  .contact-pg-contact-section .contact-map {
      margin-top: 55px;
  }
}

.contact-form-area .theme-btn {
  padding-right: 27px;
  border:0;
  margin-top: 20px;
}

.section-title-s3 h2 {
  font-weight: 700;
}

.contact-pg-contact-section .contact-map iframe {
  width: 100%;
  height: 100%;
  border: 0;
  outline: 0;
}

@media (max-width: 991px) {
  .contact-pg-contact-section .contact-form-area {
      margin-top: 80px;
  }

  .contact-pg-contact-section .contact-form-area .section-title-s3,
  .contact-pg-contact-section .contact-form-area .section-title-s4 {
      margin-bottom: 0;
  }
}

@media (max-width: 767px) {
  .contact-pg-contact-section .contact-map {
      height: 350px;
      margin-top: 45px;
  }
  .section-title-s3 h2 {
      font-size: 25px;
  }
  .contact-pg-contact-section .contact-form-area {
    margin-top: 35px;
}
}

/* My Style */
.row .form-submit .theme-btn{
  background: transparent;
  border: 1px solid #d044ff ;
}
.row .form-submit .theme-btn:hover{
  background: #d044ff;
  border: 1px solid #d044ff ;
}
.contact-pg-contact-section {
  background: linear-gradient(90deg, rgba(11,2,25,1) 0%, rgba(3,3,48,1) 100%, rgba(0,212,255,1) 100%);
}

/*---------------------------
  Fonts
----------------------------*/




/*--------------------------------------------------------------
#0.1    Theme Reset Style
--------------------------------------------------------------*/
html {
    font-size: 16px;
}

body {
    font-family: "Open Sans", sans-serif!important;
    background-color: #101010!important;
    font-size: 16px;
    font-size: 1rem;
    color: #898888;
    -webkit-font-smoothing: antialiased;
    overflow-x: hidden;
}

@media (max-width: 767px) {
    body {
        font-size: 14px;
    }
}

p {
    color: #898888;
    line-height: 1.6em;
    font-size: 16px;
    font-weight: 400;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Rubik', sans-serif;
    color: #c3bfbf;
}

ul {
    padding-left: 0;
    margin: 0!important;
    list-style: none;
}

a {
    text-decoration: none!important;
    transition: all 0.2s;
    color: #ddd;
}

a:hover {
    text-decoration: none;
}

img {
    max-width: 100%;
}

/*--------------------------------------------------------------
#0.2    Global Elements
--------------------------------------------------------------*/
.page-wrapper {
    position: relative;
    overflow: hidden;
}

.wow {
    visibility: hidden;
}

.App{
    overflow-x: hidden;
}

.fi:before {
    margin: 0;
}

.section-padding {
    padding: 100px 0;
}

@media (max-width: 991px) {
    .section-padding {
        padding: 90px 0;
    }
}
@media (max-width: 767px) {
    .section-padding {
        padding: 60px 0;
    }
}

/*** contact form error handling ***/
.contact-validation-active .error-handling-messages {
    width: 100% !important;
    margin-top: 15px !important;
}

.contact-validation-active label.error {
    color: red;
    font-size: 0.875rem;
    font-weight: normal;
    margin: 5px 0 0 0;
    text-align: left;
    display: block;
}

.contact-validation-active #loader {
    display: none;
    margin-top: 10px;
}

.contact-validation-active #loader i {
    font-size: 30px;
    font-size: 1.875rem;
    color: #f67d4a;
    display: inline-block;
    -webkit-animation: rotating linear 2s infinite;
    animation: rotating linear 2s infinite;
}

.contact-validation-active #success,
.contact-validation-active #error {
    width: 100%;
    color: #fff;
    padding: 5px 10px;
    font-size: 16px;
    text-align: center;
    display: none;
}

@media (max-width: 767px) {

    .contact-validation-active #success,
    .contact-validation-active #error {
        font-size: 15px;
    }
}

.contact-validation-active #success {
    background-color: #009a00;
    border-left: 5px solid green;
    margin-bottom: 5px;
}

.contact-validation-active #error {
    background-color: #ff1a1a;
    border-left: 5px solid red;
}

@-webkit-keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

@keyframes rotating {
    from {
        -webkit-transform: rotate(0deg);
        transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
}

.rotating {
    -webkit-animation: rotating 5s alternate infinite;
    animation: rotating 5s alternate infinite;
}

/** for popup image ***/
.mfp-wrap {
    background-color: rgba(0, 0, 0, 0.9);
    z-index: 99999;
}

.mfp-with-zoom .mfp-container,
.mfp-with-zoom.mfp-bg {
    opacity: 0;
    -webkit-backface-visibility: hidden;
    transition: all 0.3s ease-out;
}

.mfp-with-zoom.mfp-ready .mfp-container {
    opacity: 1;
}

.mfp-with-zoom.mfp-ready.mfp-bg {
    opacity: 0.8;
}

.mfp-with-zoom.mfp-removing .mfp-container,
.mfp-with-zoom.mfp-removing.mfp-bg {
    opacity: 0;
}

/*** for fancybox video ***/
.fancybox-overlay {
    background: rgba(0, 0, 0, 0.9);
    z-index: 9999 !important;
}

.fancybox-wrap {
    z-index: 99999 !important;
}

.tp-section-title {
    text-align: center;
    position: relative;
    max-width: 500px;
    margin: auto;
    margin-bottom: 70px;
}



.tp-section-title span {
    font-size: 18px;
    font-weight: 400;
    color: #f67d4a;
    display: block;
    margin-bottom: 10px;
    font-family: 'Rubik', sans-serif;
}

.tp-section-title h2 {
    font-size: 36px;
    font-weight: 700;
    margin: 0;
    display: inline-block;
    position: relative;
    line-height: 50px;
}

@media (max-width: 991px) {
    .tp-section-title h2 {
        font-size: 45px;
        font-size: 2.8125rem;
    }
}

@media (max-width: 767px) {
    .tp-section-title h2 {
        font-size: 25px;
        line-height: 35px;
    }
}

.theme-btn,
.theme-btn-s2,
.theme-btn-s4,
.theme-btn-s3 {
    background-color: #d044ff;
    color: #fff;
    font-weight: 700;
    padding: 13px 38px;
    border-radius: 5px;
    display: inline-block;
    transition: all .3s;
}

@media (max-width: 991px) {

    .theme-btn,
    .theme-btn-s2,
    .theme-btn-s4,
    .theme-btn-s3 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 20px;
    }
}


.theme-btn:hover,
.theme-btn-s2:hover,
.theme-btn-s4:hover,
.theme-btn-s3:hover,
.theme-btn:focus,
.theme-btn-s2:focus,
.theme-btn-s4:focus,
.theme-btn-s3:focus,
.theme-btn:active,
.theme-btn-s2:active,
.theme-btn-s4:active,
.theme-btn-s3:active {
    background-color: #d044ff;
    color: #fff;
}

.theme-btn-s4 {
    font-weight: 400;
    color: #fff;
    transition: all .3s;
    -moz-transition: all .3s;
    -webkit-transition: all .3s;
}

.theme-btn-s2 {
    border: none;
    background: none;
    border: 1px solid #fff;
    color: #fff;
}

.theme-btn-s2:hover,
.theme-btn-s4:hover,
.theme-btn-s2:focus,
.theme-btn-s4:focus,
.theme-btn-s2:active,
.theme-btn-s4:active {
    background-position: 100% !important;
    color: #fff;
}

.theme-btn-s3 {
    padding: 13px 30px;
}

@media (max-width: 991px) {
    .theme-btn-s3 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 25px;
    }
}

.theme-btn-s4 {
    padding: 15px 48px;
    border-radius: 55px;
}

@media (max-width: 991px) {
    .theme-btn-s4 {
        font-size: 14px;
        font-size: 0.875rem;
        padding: 10px 25px;
    }
}

.view-cart-btn {
    display: block;
    position: relative;
}

.social-links {
    overflow: hidden;
}

.social-links li {
    float: left;
    width: 35px;
    height: 35px;
    margin-right: 1px;
}

.social-links li a {
    background-color: #05f2ff;
    width: 35px;
    height: 35px;
    line-height: 35px;
    display: block;
    color: #fff;
    text-align: center;
}

.social-links li a:hover {
    background-color: #f67d4a;
}


/*** back to top **/
.back-to-top {
    background-color: rgba(254, 108, 53, 0.7);
    width: 45px;
    height: 45px;
    line-height: 45px;
    border: 2px solid #f67d4a;
    border-radius: 45px;
    text-align: center;
    display: none;
    position: fixed;
    z-index: 999;
    right: 15px;
    bottom: 15px;
    transition: all 0.2s;
  }
  
  @media (max-width: 991px) {
    .back-to-top {
      width: 35px;
      height: 35px;
      line-height: 35px;
    }
  }
  
  .back-to-top:hover {
    background-color: #f67d4a;
  }
  
  .back-to-top i {
    font-size: 18px;
    font-size: 1.125rem;
    color: #fff;
    position: absolute;
    left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }



.modal-video-close-btn {
  top: -20px;
  right: -20px;
  width: 35px;
  height: 35px;
  background: #f67d4a;
  border-radius: 50%;
}

.modal-video-close-btn:before, .modal-video-close-btn:after {
  top: 65%;
}

.errorMessage {
  font-size: 12px;
  margin-top: 5px;
  color: red;
}

.cBtnTheme {
  background: #f67d4a;
}


.fixed-navbar {
  position: relative;
  z-index: 5555;
}

.fixed-navbar.active .header-style-1,
.fixed-navbar.active .header-style-2,
.fixed-navbar.active .header-style-3{
  position: fixed;
  z-index: 555;
  top: 0;
  opacity: 1;
  left: 0;
  width: 100%;
  background: #262525;
  border: none;
  box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -moz-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
    -webkit-box-shadow: 0px 1px 5px rgba(0, 0, 0, .1);
}

/***************************************************************
My Style
***************************************************************/
.tp-breadcumb-area{
    background-attachment: fixed;
    background-size: cover;
    background-position: center;
}

